import React from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import MainCarousel from './Carousel';
import NavbarPage from './Navbar';
import "../styles/App.css"

class Home extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            colSize: 6
        }
    }

    componentDidMount() {
        window.addEventListener("resize", this.resize.bind(this));
        this.resize();
    }
    
    resize() {
        this.setState({colSize: (window.innerWidth <= 760) ? 12 : 6});
    }

    render() {

        return (
            <React.Fragment>
                <NavbarPage className="navbar"/>
                <Row>
                    <Col xs={this.state.colSize} className="col">
                        <MainCarousel />
                    </Col>
                    <Col xs={this.state.colSize} className="col section">
                        <Container>
                          <u><h2 className="header">Programa de futsal por Instagram</h2></u>
                        
                        </Container>
                    </Col>
                </Row>
            </React.Fragment>
        );
    }

}

export default Home;