import React from 'react';
import { img, Carousel } from 'react-bootstrap';

class MainCarousel extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            index: 0,
        }
    }

    render() {

        const handleSelect = (selectedIndex, e) => {
            this.setState({ index: selectedIndex });
        };

        return (
            <Carousel activeIndex={this.state.index} onSelect={handleSelect}>
                <Carousel.Item>
                    <img
                        className="d-block w-100 img"
                        src={process.env.PUBLIC_URL + "/images/equipo.jpg"}
                        alt="First slide"
                        width="400"
                        height="600"
                    />
                    <Carousel.Caption>
                        <h3>El equipo de futsal del Inst. Brown</h3>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img
                        className="d-block w-100 img"
                        src={process.env.PUBLIC_URL + "/images/jmdebonis.jpg"}
                        alt="Second slide"
                        width="400"
                        height="600"
                    />

                    <Carousel.Caption>
                        <h3>Instituto Almirante Brown</h3>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img
                        className="d-block w-100 img"
                        src={process.env.PUBLIC_URL + "/images/boxeo.jpg"}
                        alt="Third slide"
                        width="400"
                        height="600"
                    />

                    <Carousel.Caption>
                        <h3> Profesor y profesional del boxeo </h3>
                    </Carousel.Caption>
                </Carousel.Item>
            </Carousel>
        );
    }

}

export default MainCarousel;